        background: -webkit-radial-gradient(#4CCEB2, #2F8FD8); /* Для Opera 11.1 - 12.0 */ /* Для Opera 11.1 - 12.0 */ /* Для Firefox 3.6 - 15 */
        background: radial-gradient(#4CCEB2, #2F8FD8); /* Стандартный синтаксис */
